import { supabase } from '../config/supabase'

export class CycleService {
  // ëª¨ë“  ì‚¬ì´í´ ì¡°íšŒ
  static async getAllCycles() {
    try {
      // Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì€ ê²½ìš° ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ì‚¬ìš©
      if (!supabase) {
        console.warn('Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì•„ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.')
        const localCycles = localStorage.getItem('savedCycles')
        return localCycles ? JSON.parse(localCycles) : []
      }

      const { data, error } = await supabase
        .from('cycles')
        .select('*')
        .order('created_at', { ascending: false })
      
      if (error) throw error
      return data || []
    } catch (error) {
      console.error('ì‚¬ì´í´ ì¡°íšŒ ì˜¤ë¥˜:', error)
      // ì˜¤ë¥˜ ë°œìƒ ì‹œ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì—ì„œ ë³µì› ì‹œë„
      try {
        const localCycles = localStorage.getItem('savedCycles')
        return localCycles ? JSON.parse(localCycles) : []
      } catch (localError) {
        console.error('ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ë³µì› ì‹¤íŒ¨:', localError)
        return []
      }
    }
  }

  // ì‚¬ì´í´ ì €ìž¥
  static async saveCycle(cycleData) {
    try {
      // Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì€ ê²½ìš° ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ì‚¬ìš©
      if (!supabase) {
        console.warn('Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì•„ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const newCycle = {
          id: Date.now(),
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          created_at: new Date().toISOString()
        }
        localCycles.unshift(newCycle)
        localStorage.setItem('savedCycles', JSON.stringify(localCycles))
        return newCycle
      }

      const { data, error } = await supabase
        .from('cycles')
        .insert([{
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          created_at: new Date().toISOString()
        }])
        .select()
      
      if (error) throw error
      return data[0]
    } catch (error) {
      console.error('ì‚¬ì´í´ ì €ìž¥ ì˜¤ë¥˜:', error)
      // ì˜¤ë¥˜ ë°œìƒ ì‹œ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì— ì €ìž¥
      try {
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const newCycle = {
          id: Date.now(),
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          created_at: new Date().toISOString()
        }
        localCycles.unshift(newCycle)
        localStorage.setItem('savedCycles', JSON.stringify(localCycles))
        return newCycle
      } catch (localError) {
        console.error('ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ì €ìž¥ ì‹¤íŒ¨:', localError)
        throw new Error('ì‚¬ì´í´ ì €ìž¥ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
      }
    }
  }

  // ì‚¬ì´í´ ìˆ˜ì •
  static async updateCycle(id, cycleData) {
    try {
      // Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì€ ê²½ìš° ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ì‚¬ìš©
      if (!supabase) {
        console.warn('Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì•„ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const index = localCycles.findIndex(cycle => cycle.id === id)
        if (index > -1) {
          localCycles[index] = {
            ...localCycles[index],
            name: cycleData.name,
            content: cycleData.content,
            selected_items: cycleData.selectedItems,
            updated_at: new Date().toISOString()
          }
          localStorage.setItem('savedCycles', JSON.stringify(localCycles))
          return localCycles[index]
        }
        throw new Error('ì‚¬ì´í´ì„ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.')
      }

      const { data, error } = await supabase
        .from('cycles')
        .update({
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          updated_at: new Date().toISOString()
        })
        .eq('id', id)
        .select()
      
      if (error) throw error
      return data[0]
    } catch (error) {
      console.error('ì‚¬ì´í´ ìˆ˜ì • ì˜¤ë¥˜:', error)
      throw new Error('ì‚¬ì´í´ ìˆ˜ì •ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
    }
  }

  // ì‚¬ì´í´ ì‚­ì œ
  static async deleteCycle(id) {
    try {
      // Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì€ ê²½ìš° ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ì‚¬ìš©
      if (!supabase) {
        console.warn('Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì•„ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const filteredCycles = localCycles.filter(cycle => cycle.id !== id)
        localStorage.setItem('savedCycles', JSON.stringify(filteredCycles))
        return true
      }

      const { error } = await supabase
        .from('cycles')
        .delete()
        .eq('id', id)
      
      if (error) throw error
      return true
    } catch (error) {
      console.error('ì‚¬ì´í´ ì‚­ì œ ì˜¤ë¥˜:', error)
      throw new Error('ì‚¬ì´í´ ì‚­ì œì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
    }
  }

  // ì‚¬ì´í´ IDë¡œ ì¡°íšŒ
  static async getCycleById(id) {
    try {
      // Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì€ ê²½ìš° ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ ì‚¬ìš©
      if (!supabase) {
        console.warn('Supabaseê°€ ì„¤ì •ë˜ì§€ ì•Šì•„ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        return localCycles.find(cycle => cycle.id === id)
      }

      const { data, error } = await supabase
        .from('cycles')
        .select('*')
        .eq('id', id)
        .single()
      
      if (error) throw error
      return data
    } catch (error) {
      console.error('ì‚¬ì´í´ ì¡°íšŒ ì˜¤ë¥˜:', error)
      throw new Error('ì‚¬ì´í´ì„ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.')
    }
  }
}
