

export function useCodeGeneration() {
  const feederCodes = {
    1: { on: 'M60', off: 'M70' },
    2: { on: 'M61', off: 'M71' },
    3: { on: 'M62', off: 'M72' }
  }
  
  const generate3DPrintingCode = (params, buttonCodes, updateTextArea) => {
    const {
      layerThickness,
      dwellTime,
      target,
      selectedFeeder,
      selectedCycle,
      scanSpeed
    } = params
    
    console.log('í”¼ë” ì„ íƒ:', selectedFeeder)
    const generatedCode = generateCodeContent(
      layerThickness,
      dwellTime,
      target,
      selectedFeeder,
      selectedCycle,
      scanSpeed,
      buttonCodes
    )
    
    updateTextArea(generatedCode)
    return generatedCode
  }
  
  const generateCodeContent = (
    layerThickness,
    dwellTime,
    target,
    feeder,
    cycle,
    scanSpeed,
    buttonCodes
  ) => {
    console.log('generateCodeContent í˜¸ì¶œë¨:', { 
      layerThickness, 
      dwellTime, 
      target, 
      feeder, 
      cycle, 
      scanSpeed
    })
    
    // ì´ ë ˆì´ì–´ ìˆ˜ ê³„ì‚°
    const totalLayers = Math.ceil(target / layerThickness)
    
    // ì„ íƒëœ ì‚¬ì´í´ì˜ ë²„íŠ¼ ì½”ë“œë“¤ ê°€ì ¸ì˜¤ê¸°
    let cycleButtonCodes = []
    if (cycle.selectedItems && cycle.selectedItems.length > 0) {
      cycleButtonCodes = cycle.selectedItems.map(item => {
        return buttonCodes[item.name] || `; ${item.name} - No code assigned`
      })
    } else {
      // ê¸°ë³¸ C1 ì‚¬ì´í´ í…œí”Œë¦¿ ì‚¬ìš©
      cycleButtonCodes = [buttonCodes['C1'] || `; C1 - No code assigned`]
    }
    
    // í”¼ë” ê°’ì„ ìˆ«ìžë¡œ ë³€í™˜
    const feederNumber = parseInt(feeder) || 1
    const selectedFeeder = feederCodes[feederNumber] || feederCodes[1]
    
    console.log('í”¼ë” ë§¤ê°œë³€ìˆ˜:', feeder)
    console.log('í”¼ë” íƒ€ìž…:', typeof feeder)
    console.log('ì„ íƒëœ í”¼ë” ì½”ë“œ:', selectedFeeder)
    
    let generatedCode = ''
    
    // 1. í”„ë¡œê·¸ëž¨ í—¤ë” ìƒì„±
    generatedCode += generateProgramHeader(selectedFeeder, dwellTime, scanSpeed)
    
    // 2. ë ˆì´ì–´ ë£¨í”„ ì‹œìž‘
    for (let layer = 1; layer <= totalLayers; layer++) {
      const currentZ = ((layer - 1) * layerThickness).toFixed(3)
      
      generatedCode += generateLayerHeader(layer, currentZ)
      
      // 2-1. ì„ íƒëœ ì‚¬ì´í´ì˜ ë²„íŠ¼ ì½”ë“œë“¤ì„ í˜„ìž¬ Z ë†’ì´ì— ë§žì¶° ìƒì„±
      generatedCode += generateCycleButtonCodes(
        cycleButtonCodes,
        cycle.selectedItems,
        currentZ,
        dwellTime,
        buttonCodes,
        layerThickness,
        selectedFeeder
      )
    }
    
    // 3. í”„ë¡œê·¸ëž¨ í‘¸í„° ìƒì„±
    generatedCode += generateProgramFooter(selectedFeeder, dwellTime)
    
    return generatedCode.trim()
  }
  
  const generateProgramHeader = (selectedFeeder, dwellTime, scanSpeed) => {
    let code = ''
    
    code += '; === í”„ë¡œê·¸ëž¨ í—¤ë” ===\n'
    code += 'G90\n'
    code += 'M50\n'
    code += `F${scanSpeed}\n`
    code += 'G54\n'
    code += 'M40\n'
    code += `${selectedFeeder.on}\n`
    code += `G4 P${dwellTime}\n`
    code += '\n'
    
    return code
  }
  
  const generateLayerHeader = (layer, currentZ) => {
    let code = ''
    
    code += `; === Layer ${layer} (Z = ${currentZ}) ===\n`
    
    return code
  }
  
  const generateProgramFooter = (selectedFeeder, dwellTime) => {
    let code = ''
    
    code += '; === í”„ë¡œê·¸ëž¨ í‘¸í„° ===\n'
    code += `${selectedFeeder.off}\n`
    code += 'M41\n'
    code += 'M51\n'
    code += 'M53\n'
    code += 'G0 X0.0 Y0.0\n'
    code += `G4 P${dwellTime}\n`
    code += 'M90\n'
    code += 'G53\n'
    code += 'M30\n'
    
    return code
  }
  
  const generateLayerChangeBlock = (selectedFeeder, dwellTime) => {
    let code = ''
    
    code += '; ë ˆì´ì–´ ë³€ê²½ ë¸”ë¡\n'
    code += 'M51\n'
    code += `${selectedFeeder.off}\n`
    code += 'M41\n'
    code += `G4 P${dwellTime}\n`
    code += 'M50\n'
    code += 'M40\n'
    code += `${selectedFeeder.on}\n`
    
    return code
  }
  
  const generateCycleButtonCodes = (cycleButtonCodes, selectedItems, currentZ, dwellTime, buttonCodes, layerThickness, selectedFeeder) => {
    let code = ''
    
    // ìŠ¬ëž˜ì‹œë¡œ êµ¬ë¶„ëœ ê·¸ë£¹ë“¤ì„ ì°¾ê¸°
    const groups = []
    let currentGroup = []
    
    selectedItems.forEach(item => {
      if (item.name === '/') {
        if (currentGroup.length > 0) {
          groups.push(currentGroup)
          currentGroup = []
        }
      } else {
        currentGroup.push(item)
      }
    })
    
    // ë§ˆì§€ë§‰ ê·¸ë£¹ ì¶”ê°€
    if (currentGroup.length > 0) {
      groups.push(currentGroup)
    }
    
    // ê·¸ë£¹ì´ ì—†ìœ¼ë©´ ì „ì²´ë¥¼ í•˜ë‚˜ì˜ ê·¸ë£¹ìœ¼ë¡œ ì²˜ë¦¬
    if (groups.length === 0) {
      groups.push(selectedItems.filter(item => item.name !== '/'))
    }
    
    // í˜„ìž¬ Z ë†’ì´ì—ì„œ ë ˆì´ì–´ ë²ˆí˜¸ ê³„ì‚° (layerThicknessë¡œ ë‚˜ëˆ„ì–´ì„œ)
    // ì˜ˆ: Z=0.1, layerThickness=0.1ì´ë©´ layer=1, Z=0.2ë©´ layer=2
    const layerNumber = Math.floor(parseFloat(currentZ) / layerThickness) + 1
    
    // ë ˆì´ì–´ ë²ˆí˜¸ë¡œ ê·¸ë£¹ ì„ íƒ (1ë¶€í„° ì‹œìž‘í•˜ëŠ” ë ˆì´ì–´ ë²ˆí˜¸ ì‚¬ìš©)
    // ë ˆì´ì–´ 1: ê·¸ë£¹ 0, ë ˆì´ì–´ 2: ê·¸ë£¹ 1, ë ˆì´ì–´ 3: ê·¸ë£¹ 0, ë ˆì´ì–´ 4: ê·¸ë£¹ 1...
    const groupIndex = (layerNumber - 1) % groups.length
    const selectedGroup = groups[groupIndex] || groups[0] || []
    
    console.log(`ë ˆì´ì–´ ${layerNumber} (Z=${currentZ}), ì„ íƒëœ ê·¸ë£¹ ${groupIndex}:`, selectedGroup)
    
    if (selectedGroup.length > 0) {
      const firstItem = selectedGroup[0]
      const firstButtonCode = buttonCodes[firstItem.name] || `; ${firstItem.name} - No code assigned`
      
      // ì²« ë²ˆì§¸ ë²„íŠ¼ ì½”ë“œë¥¼ ì¤„ë³„ë¡œ ë¶„ë¦¬
      const codeLines = firstButtonCode.split('\n')
      
      // ì²« ë²ˆì§¸ G0 ëª…ë ¹ì–´ë§Œ ì°¾ì•„ì„œ ì²˜ë¦¬
      let firstG0Found = false
      let remainingCode = ''
      
      for (const line of codeLines) {
        if (!firstG0Found && line.trim().startsWith('G0')) {
          // ì²« ë²ˆì§¸ G0 ëª…ë ¹ì–´ë¥¼ í˜„ìž¬ Z ë†’ì´ì— ë§žì¶° ìˆ˜ì •
          let modifiedG0 = line
            .replace(/Z[0-9]*\.?[0-9]+/g, `Z${currentZ}`)
            .replace(/G4 P[0-9]+/g, `G4 P${dwellTime}`)
          
          code += `; ${firstItem.name}\n`
          code += modifiedG0 + '\n'
          firstG0Found = true
          
          // ì²« ë²ˆì§¸ G0 ëª…ë ¹ì–´ ë‹¤ìŒì— ë ˆì´ì–´ ë³€ê²½ ë¸”ë¡ ì¶”ê°€ (ì²« ë²ˆì§¸ ë ˆì´ì–´ ì œì™¸)
          if (layerNumber > 1) {
            code += generateLayerChangeBlock(selectedFeeder, dwellTime)
            code += '; ë‚˜ë¨¸ì§€ ë™ìž‘ ì½”ë“œ\n'
          }
          
          // ë‚˜ë¨¸ì§€ ì½”ë“œëŠ” ê·¸ëŒ€ë¡œ ì¶”ê°€
          remainingCode = codeLines.slice(codeLines.indexOf(line) + 1).join('\n')
          break
        }
      }
      
      // ì²« ë²ˆì§¸ G0ë¥¼ ì°¾ì§€ ëª»í•œ ê²½ìš° ì „ì²´ ì½”ë“œë¥¼ ê·¸ëŒ€ë¡œ ì‚¬ìš©
      if (!firstG0Found) {
        code += `; ${firstItem.name}\n`
        let modifiedCode = firstButtonCode
          .replace(/Z[0-9]*\.?[0-9]+/g, `Z${currentZ}`)
          .replace(/G4 P[0-9]+/g, `G4 P${dwellTime}`)
        code += modifiedCode + '\n'
      } else if (remainingCode.trim()) {
        // ë‚˜ë¨¸ì§€ ì½”ë“œê°€ ìžˆìœ¼ë©´ Z ë†’ì´ì™€ dwell timeë§Œ ìˆ˜ì •í•´ì„œ ì¶”ê°€
        let modifiedRemainingCode = remainingCode
          .replace(/Z[0-9]*\.?[0-9]+/g, `Z${currentZ}`)
          .replace(/G4 P[0-9]+/g, `G4 P${dwellTime}`)
        code += modifiedRemainingCode + '\n'
      }
      
      // ë‚˜ë¨¸ì§€ ì•„ì´í…œë“¤ì˜ ì½”ë“œ ìƒì„±
      for (let i = 1; i < selectedGroup.length; i++) {
        const item = selectedGroup[i]
        const buttonCode = buttonCodes[item.name] || `; ${item.name} - No code assigned`
        code += `; ${item.name}\n`
        
        // ë²„íŠ¼ ì½”ë“œë¥¼ í˜„ìž¬ Z ë†’ì´ì— ë§žì¶° ìˆ˜ì •
        let modifiedCode = buttonCode
          // Z ê°’ êµì²´
          .replace(/Z[0-9]*\.?[0-9]+/g, `Z${currentZ}`)
          // G4 P ê°’ êµì²´
          .replace(/G4 P[0-9]+/g, `G4 P${dwellTime}`)
        
        code += modifiedCode + '\n\n'
      }
    }
    
    return code
  }
  
  return {
    generate3DPrintingCode,
    generateCodeContent
  }
}
